.contact {
	grid-column: 1 / 2;
	min-height: 170px;
	background-color: var(--color-primary);
	border-radius: 16px;
	padding: 20px;
}

.contactHeader {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 10px;
	gap: 20px;
}

.categories {
	margin-bottom: 10px;
}

.categories .categoriesList {
	gap: 10px;
	list-style: none;
	padding: 0;
	margin: 0;
}

.categories .categoriesList button {
	padding: 5px;
	color: black;
	background-color: var(--color-light);
	border: none;
	border-radius: 5px;
	font-size: 12px;
	cursor: pointer;
	transition: all 0.2s ease-in-out;
	white-space: nowrap;
}

.categories .categoriesList button:hover {
	background-color: var(--color-primary-hover);
	color: var(--color-light);
	transform: scale(1.1);
}

.desktopOnly {
	display: flex;
}

.mobileOnly {
	display: none;
}

.arrows button {
	color: black;
	background-color: var(--color-light);
	border: none;
	border-radius: 50%;
	width: 40px;
	height: 40px;
	margin-left: 10px;
	cursor: pointer;
	transition: all 0.2s ease-in-out;
}

.arrows button:hover {
	background-color: var(--color-primary-hover);
	color: var(--color-light);
	transform: scale(1.1);
}

.contactInfo {
	display: flex;
	gap: 20px;
	flex-wrap: wrap;
	justify-content: flex-start;
}

.doctorInfo p {
	font-size: 14px;
	color: black;
}

.doctorCard {
	display: flex;
	align-items: center;
	background-color: var(--color-light);
	padding: 15px;
	border-radius: 12px;
	width: calc(33.333% - 20px);
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
	transition: transform 0.2s ease-in-out;
	flex-shrink: 0;
}

.doctorCard:hover {
	transform: scale(1.02);
}

.doctorImg {
	flex-shrink: 0;
	width: 80px;
	height: 80px;
	border-radius: 50%;
	overflow: hidden;
	margin-right: 15px;
}

.doctorImg img {
	width: 100%;
	height: 100%;
	-o-object-fit: cover;
	   object-fit: cover;
}

.doctorInfo {
	display: flex;
	flex-direction: column;
	gap: 5px;
}

.doctorName {
	font-weight: 600;
	font-size: 16px;
}

.doctorSpecialty {
	font-size: 14px;
}

.messageBtn {
	margin-top: 8px;
	background-color: var(--color-primary);
	color: var(--color-light);
	border: none;
	border-radius: 8px;
	padding: 6px 12px;
	cursor: pointer;
	display: flex;
	justify-content: center;
	max-width: 110px;
	justify-content: center;
	align-items: center;
	gap: 6px;
	transition: background-color 0.2s ease-in-out;
}

.messageBtn:hover {
	background-color: var(--color-primary-hover);
}

.paginationDots {
	display: flex;
	gap: 8px;
	justify-content: center;
	align-items: center;
	margin-top: 12px;
}

.dot {
	width: 10px;
	height: 10px;
	background-color: #ccc;
	border-radius: 50%;
	display: inline-block;
	transition: background-color 0.3s;
	cursor: pointer;
}

.activeDot {
	background-color: #333;
}

@media (min-width: 768px) and (max-width: 1550px),
	(min-width: 480px) and (max-width: 600px) {
	.doctorCard {
		flex-direction: column;
		align-items: center;
		text-align: center;
	}

	.doctorImg {
		margin-right: 0;
		margin-bottom: 10px;
	}
}

@media (max-width: 768px) {
	.doctorCard {
		width: calc(50% - 20px);
	}

	.arrows button {
		width: 36px;
		height: 36px;
		font-size: 16px;
	}
	.categories .categoriesList {
		flex-wrap: wrap;
		justify-content: center;
	}

	.categories .categoriesList button {
		font-size: 12px;
		padding: 5px 10px;
	}
}

@media (max-width: 480px) {
	.doctorCard {
		width: 100%;
	}

	.arrows button {
		width: 32px;
		height: 32px;
		font-size: 14px;
		margin-left: 5px;
	}
	.categories .categoriesList {
		gap: 6px;
	}

	.categories .categoriesList button {
		font-size: 11px;
		padding: 4px 8px;
	}
	.desktopOnly {
		display: none;
	}
	.mobileOnly {
		display: block;
		width: 100%;
		padding: 8px;
		border-radius: 8px;
		border: 1px solid #ccc;
		font-size: 14px;
	}
}

.floatingClose {
	position: fixed;
	bottom: 1rem;
	right: 1rem;
	width: 50px;
	height: 50px;
	border-radius: 50%;
	background-color: tomato;
	color: white;
	border: none;
	display: flex;
	align-items: center;
	justify-content: center;
	box-shadow: 0 2px 6px rgba(0, 0, 0, 0.25);
	cursor: pointer;
	transition: background-color 0.2s;
	z-index: 10000;
}

.chatIcon {
	animation: growIn 0.5s ease forwards;
}

.floatingClose:hover {
	background-color: rgb(255, 69, 36);
}

@keyframes growIn {
	0% {
		transform: scale(0);
		opacity: 0;
	}
	60% {
		transform: scale(1.2);
		opacity: 1;
	}
	100% {
		transform: scale(1);
	}
}
